/* General styles */
.container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  color: #333;
}

.section {
  margin-bottom: 30px;
}

.subtitle {
  margin-bottom: 15px;
  font-size: 1.2rem;
  color: #333;
}

.input-row {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin-bottom: 15px;
}

.ledger-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.balance-section {
  display: flex;
  align-items: center;
  gap: 10px;
}

.balance-section h3 {
  margin: 0;
  font-size: 1.1rem;
  color: #333;
}

.table-container {
  overflow-x: auto;
}

.ledger-table {
  width: 100%;
  border-collapse: collapse;
  font-family: Arial, sans-serif; /* Consistent font for the table */
  font-size: 14px; /* Consistent font size for the table */
}

.ledger-table th,
.ledger-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.ledger-table th {
  background-color: #f5f5f5;
  font-weight: bold;
}

.ledger-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.ledger-table tr:hover {
  background-color: #f1f1f1;
}

/* Ensure amount cells are aligned to the right */
.ledger-table td:nth-child(5) {
  text-align: right;
  font-family: inherit; /* Inherit font from table */
  font-size: inherit; /* Inherit font size from table */
}

/* Style for positive amounts (green) */
.positive-amount {
  color: green;
  font-family: inherit; /* Inherit font from table */
  font-size: inherit; /* Inherit font size from table */
}

/* Style for negative amounts (red and in brackets) */
.negative-amount {
  color: red;
  font-family: inherit; /* Inherit font from table */
  font-size: inherit; /* Inherit font size from table */
}

/* Add brackets for negative amounts */
.negative-amount::before {
  content: "(";
}

.negative-amount::after {
  content: ")";
}

/* Fix for overlapping labels in Material-UI TextField */
.MuiFormControl-root {
  margin-top: 8px;
  margin-bottom: 8px;
}

.MuiInputLabel-outlined {
  background-color: white;
  padding: 0 5px;
}

/* Button styles */
.button-container {
  display: flex;
  gap: 10px;
  margin-top: 15px;
}

/* Responsive styles */
@media (max-width: 768px) {
  .form-grid {
    grid-template-columns: 1fr;
  }

  .input-row {
    flex-direction: column;
    align-items: flex-start;
  }

  .input-row .MuiTextField-root {
    width: 100%;
  }

  .input-row .MuiButton-root {
    width: 100%;
    margin-left: 0 !important;
    margin-top: 10px;
  }

  .ledger-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .balance-section {
    flex-direction: column;
    align-items: flex-start;
  }
}